
@import "../../styles/model.css";
.warp[data-v-56d74ac5]{
      width: 100%;
      height: 400px;
      background: palevioletred;
      float: none;
}
.div1[data-v-56d74ac5]{
      background: transparent;
      width: 0px;
      height: 0px;
      border-left: 30px solid transparent;
      border-right: 30px solid blue;
      border-bottom: 30px solid transparent;
      border-top: 30px solid transparent;
}
.div2[data-v-56d74ac5]{
      color: black;
      font-weight: bold;
      font-size: large;
}
.warp div[data-v-56d74ac5]{
      width:120px;
      height:120px;
      line-height:120px;
      margin: 20px;
      background-color: #5cb85c;
      float: left;
      font-size: 12px;
      text-align: center;
      color:orangered;
}
.transition1[data-v-56d74ac5] {
      transition: All 1s ease-in-out;
      -webkit-transition: All 1s ease-in-out;
      -moz-transition: All 1s ease-in-out;
      -o-transition: All 1s ease-in-out;
}
.transition4[data-v-56d74ac5] {
      transition: All 0.4s ease-in-out;
      -webkit-transition: All 0.4s ease-in-out;
      -moz-transition: All 0.4s ease-in-out;
      -o-transition: All 0.4s ease-in-out;
}

  /*效果一：360°旋转 修改rotate(旋转度数)*/
.rotateY[data-v-56d74ac5]:hover {
      transform: rotateY(180deg);
      -webkit-transform: rotateY(180deg);
      -moz-transform: rotateY(180deg);
      -o-transform: rotateY(180deg);
      -ms-transform: rotateY(180deg);
}
  /*效果二：放大 修改scale(放大的值)*/
.scale[data-v-56d74ac5]:hover {
      transform: scale(1.2);
      -webkit-transform: scale(1.2);
      -moz-transform: scale(1.2);
      -o-transform: scale(1.2);
      -ms-transform: scale(1.2);
}
  /*效果三：旋转放大 修改rotate(旋转度数) scale(放大值)*/
.rotateScale[data-v-56d74ac5]:hover {
      transform: rotate(360deg) scale(1.2);
      -webkit-transform: rotate(360deg) scale(1.2);
      -moz-transform: rotate(360deg) scale(1.2);
      -o-transform: rotate(360deg) scale(1.2);
      -ms-transform: rotate(360deg) scale(1.2);
}
  /*效果四：上下左右移动 修改translate(x轴,y轴)*/
.translate[data-v-56d74ac5]:hover {
      transform: translate(0, -10px);
      -webkit-transform: translate(0, -10px);
      -moz-transform: translate(0, -10px);
      -o-transform: translate(0, -10px);
      -ms-transform: translate(0, -10px);
}



